body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  width: 100%;
  min-height: 100vh;

  background-color: #093395;  
  padding: 20px 0;
}

* {
  font-family: Helvetica, sans-serif;
  font-weight: 200;
}

h1,h2,h3,h4,h5 {
  font-weight: bold;
}

.App {
  background-color: rgba(240, 248, 255, 0.723);

  padding: 20px;
  border-radius: 10px;

  max-width: 420px;
}
@media screen and (max-width: 420px){
  .App {
    border-radius: 0;
  }
}

.Title > h3 {
  text-align: center;
}

.form-group {
  width: 100%;
  margin: 10px 0;
}

.form-group > label {
  margin: 5px 0;
  font-size: 1rem;
  display: block;
  font-weight: bold;
}

.form-group > input[type="number"] {
  width: calc(95% - 20px);
  font-size: 1rem;
  padding: 10px 15px;
  border: 0;
  border-radius: 5px;
  margin: 10px 0;
}

.form-group > input[type="range"] {
  width: calc(95%);
  color: aliceblue;
}


